.profile__name{
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 114%;
  color: #fff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 300px;
}

@media screen and (max-width: 1023px) {
  .profile__name{
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    max-width: 230px;
  }
}

@media screen and (max-width: 767px) {
  .profile__name{
    margin: 0 0 14px;
  }
}

